<template>
  <div class="search">
    <input placeholder="搜索" v-model="keyword">
    <button type="button" name="button" @click="search">点击搜索</button>
    <div class="result">
      <cell v-for="item in result" :link="{path: '/bookinfo', query: {bookId: item.id}}">
        <div class="booklist">
          <div class="book-status">
            <h4>{{item.name}}<span class="statu">连载</span></h4>
          </div>
          <div class="book-des">
            <p>暂时没有简介呢，联系管理员添加</p>
          </div>
          <div class="book-info">
            <p>{{item.author}} | <span style="color: #b93321">{{parseInt(item.size)/10}}</span>万字</p>
            <p class="book-type">{{item.typeName}}</p>
          </div>
        </div>
      </cell>
    </div>
  </div>
</template>

<script>
import { startSearching } from 'api/user'
import Cell from 'components/Cell.vue'
export default {
  data() {
    return {
      keyword: '',
      result: ''
    }
  },
  methods: {
    search () {
      if (this.keyword.length !== 0) {
        startSearching(this.keyword).then(res => {
          this.result = res.ret.contentlist
        })
      } else {
        alert('请输入关键字')
      }
    }
  },
  components: {
    Cell
  }
}
</script>



