<template>
  <div class="header">
    <div class="avatar">
      <img :src="userInfo.headimgurl" alt="">
    </div>

    <router-link class="recommend" to="/recommend">
      <h5><i class="i i-hot"></i>推荐</h5>
    </router-link>
    <router-link class="search" to="search">
      <h5><i class="i i-sousuo"></i>搜索</h5>
    </router-link>
  </div>
</template>

<script>
export default {
  mounted() {
  },
  data() {
    return {
      userInfo: JSON.parse(localStorage.getItem('user'))
    }
  }
}
</script>

<style lang="scss">
.header {
  background: #fff;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  align-items: center;
  .avatar img {
    height: 40px;
  }
  h5 {
    font-weight: 600;
    font-size: 12px;
    color: #444;
  }
  .i-hot {
    color: #b93321
  }
}
</style>



