<template>
  <div id="app">
    <s-header></s-header>
    <router-view></router-view>
  </div>
</template>

<script>
import SHeader from 'components/Header.vue'
import './styles/index.scss'
import { getUserInfo } from 'api/user'
export default {
  name: 'app',
  mounted() {
    getUserInfo().then(res => {
      console.log(res.ret.book_list);
      localStorage.setItem('user', JSON.stringify(res.ret))
      this.userInfo = res.ret
    }).catch(err => confirm(err.message))
  },
  data() {
    return {
      userInfo : JSON.parse(localStorage.getItem('user'))
    }
  },
  methods: {
    test() {
      alert(JSON.stringify(this.userInfo))
    }
  },
  components: {
    SHeader
  }
}
</script>

<style lang="scss">
body {
  padding: 0;
  margin: 0;
  font-family: PingFang SC,Helvetica Neue,Hiragino Sans GB,Microsoft Yahei,WenQuanYi Micro Hei,sans-serif;
  background: #f8f8f8
}
</style>



