<template>
  <div class="book-content">
    <div class="content" v-html="content">

    </div>
  </div>
</template>

<script>
import { startReading } from 'api/user'
export default {
  mounted() {
    startReading(this.$route.params).then(res => {
      this.content = res.showapi_res_body.txt
    })
  },
  data() {
    return {
      content: ''
    }
  }
}
</script>



