<template>
  <div class="home">
    <div class="empty-list" v-if="booklist.length === 0">
      <p>书架空空如也，去逛逛吧！</p>
    </div>
    <cell v-else v-for="item in booklist" :link="{path: '/bookinfo', query: {bookId: item.id}}">
      <div class="booklist">
        <div class="book-status">
          <h4>{{item.name}}<span class="statu">连载</span></h4>
        </div>
        <div class="book-des">
          <p>暂时没有简介呢，联系管理员添加</p>
        </div>
        <div class="book-info">
          <p>{{item.author}} | <span style="color: #b93321">{{parseInt(item.size)/10}}</span>万字</p>
          <p class="book-type">{{item.typeName}}</p>
        </div>
      </div>
    </cell>
  </div>
</template>

<script>
import Cell from 'components/Cell.vue'
import { getUserInfo } from 'api/user'
export default {
  mounted() {
    getUserInfo().then(res => {
      localStorage.setItem('user', JSON.stringify(res.ret))
      this.booklist = res.ret.book_list
    }).catch(err => confirm(err.message))
  },
  data() {
    return {
      booklist: ''
    }
  },
  components: {
    Cell
  }
}
</script>

<style lang="css">
</style>



