import axios from 'axios'

const instance = axios.create({
  baseURL: 'http://wechat.xiadd.me/api',
  timeout: 5000,
  withCredentials: true
})

//获取用户信息
function getUserInfo () {
  return instance.get('/wechat/user').then(res => res.data)
}

//将书添加到书架
function addToList (bookId) {
  return instance.put('/add/' + bookId).then(res => res.data)
}

//将书移出书架
function removeFromList (bookId) {
  return instance.delete('/remove/' + bookId).then(res => res.data)
}

//开始阅读
function startReading (params) {
  return instance.get('/chapter', {params: params}).then(res => res.data)
}

//搜索
function startSearching (keyword) {
  return instance.get('/books', {params: { keyword: keyword }}).then(res => res.data)
}

module.exports = {
  getUserInfo,
  addToList,
  removeFromList,
  startReading,
  startSearching
}



