import axios from 'axios'

const instance = axios.create({
  baseURL: 'http://wechat.xiadd.me/api',
  timeout: 5000,
  withCredentials: true
})

function queryBookById (bookId) {
  return instance.get('/books', { params: { bookId: bookId } }).then(res => res.data)
}

function getBookContent (bookId) {
  return instance.get('/chapters/' + bookId).then(res => res.data.chapterList)
}

module.exports = {
  queryBookById,
  getBookContent
}



