<template>
  <div class="bookinfo">
    <div class="all-content" v-if="showContent">
      <router-link :to="{name: 'content', params: { bookId: item.bookId, cid: item.cid }}" class="single-content" v-for="item in bookinfo.chapterList">
        {{item.name}}
      </router-link>
      <div class="close-content" @click="toggleContent">
        <div class="close-btn">
          X
        </div>
      </div>
    </div>
    <div class="book-title-info">
      <h5>{{bookinfo.name}}</h5>
      <div class="book-detail">
        <p><span class="book-author">{{bookinfo.author}}</span> &nbsp&nbsp| &nbsp&nbsp<span>{{bookinfo.typeName}}</span> &nbsp&nbsp|&nbsp&nbsp <span>{{parseInt(bookinfo.size)/10}}万字</span></p>
      </div>
      <div class="update-time">
        {{bookinfo.updateTime}}更新
      </div>
    </div>

    <div class="book-action">
      <a class="button-link add-list" @click="changeBookStatu">
        <div class="">
          {{ statu ? '+加入书架' : '-移出书架' }}
        </div>
      </a>
      <a class="button-link view-content" @click="toggleContent">
        <div class="">
          查看目录
        </div>
      </a>
      <a class="button-link start-read">
        <div class="">
          开始阅读
        </div>
      </a>
    </div>
  </div>
</template>

<script>
import { queryBookById, getBookContent } from 'api/book'
import { removeFromList, addToList } from 'api/user'
export default {
  mounted() {
    queryBookById(this.$route.query.bookId).then(res => {
      this.bookinfo = res.ret[0]
      if (this.bookinfo.chapterList.length === 0) {
        getBookContent(this.$route.query.bookId).then(result => {
          this.bookinfo.chapterList = result
        })
      }
    })
  },
  data() {
    return {
      bookinfo: '',
      statu: JSON.parse(localStorage.getItem('user')).book_list.every(v => this.$route.query.bookId !== v.id),
      showContent: false
    }
  },
  methods: {
    changeBookStatu() {
      let id = this.bookinfo.id
      if (!this.statu) {
        removeFromList(id).then(res => {
          if (res.code === 1) {
            this.statu = !this.statu
          }
        })
      } else {
        addToList(id).then(res => {
          if (res.code === 1) {
            this.statu = !this.statu
          }
        })
      }
    },
    toggleContent() {
      this.showContent = !this.showContent
    }
  },
  computed: {
    inList: function () {
      let booklist = JSON.parse(localStorage.getItem('user')).book_list
      let tmp = booklist.every(v => this.$route.query.bookId !== v.id)
      return !tmp
    }
  }
}
</script>



